import React, { useRef } from "react";
import { AlertDialog, AlertDialogBody, AlertDialogContent, AlertDialogFooter, AlertDialogHeader, AlertDialogOverlay, Button, useColorModeValue } from "@chakra-ui/react";
import { useAlertsContext } from "../context/AlertsProvider";

const DeleteTransactionAlert = () => {

    const { isDeleteOpen, onDeleteClose, deleteTransaction } = useAlertsContext();
    const cancelRef = useRef();
    const bg = useColorModeValue("white", "darkMode.bgWrapperPrimary");

    return (
        <AlertDialog
            isOpen={isDeleteOpen}
            leastDestructiveRef={cancelRef}
            onClose={onDeleteClose}
            isCentered={true}
        >
            <AlertDialogOverlay>
                <AlertDialogContent margin={{ base: '0 1rem', lg: '' }} bg={bg}>
                    <AlertDialogHeader fontSize="lg" fontWeight="bold">Usun transakcj?</AlertDialogHeader>
                    <AlertDialogBody>Czy na pewno chcesz usun t transakcj? Tej operacji nie mona cofn.</AlertDialogBody>
                    <AlertDialogFooter>
                        <Button ref={cancelRef} onClick={onDeleteClose}>Anuluj</Button>
                        <Button colorScheme="red" onClick={deleteTransaction} ml={3}>Usu</Button>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialogOverlay>
        </AlertDialog>
    )
}
export default DeleteTransactionAlert;